@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
body {
  background-color: rgba(234, 237, 237);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
}
.header{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #131921;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100 ;

}
.header__logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;

}
.header__search{
    display: flex;
    flex: 1 1;
    align-items: center;
    border-radius: 24px;
}
.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header__searchIcon{
    padding: 5px;
    height:22px !important;
    background-color: #cd9042;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
    /* text-align: center;
    padding: 10px; */
}
.header__optionBasket{
    display: flex;
    align-items: center;
    color: #fff;
}
.header__optionBasketCount{
    margin-left: 10px;
    margin-right: 10px;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: #fff;
}
.header__optionLineOne{
    font-size: 10px;
}
.header__optionLineTwo{
font-size: 13px;
font-weight: 800;
}
.login__user{
    font-size: 11px;
}
.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home__row{
    display: flex;
    /* z-index: 1; */
    margin-top: 10px;
    margin-left: 15px;
    margin-right: 15px;
   transition: 0.5s;
}
.home__row:hover{
    padding: 50px;
    /* background-image: url('	https://img.freepik.com/free-photo/shocked-two-wom…ing-mobile-phone_171337-5718.jpg?size=626&ext=jpg'); */
    background-color: rgba(0, 0, 0, 0.1);
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
}
 .home__rowBackground{
    background-color:#fff;
}
