.product{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: #fff;
    z-index: 1;
  transition: 0.3s;
}
.product:hover{
    /* max-height: 800px; */
/* background-color: rgba(0, 0, 0, 0.5); */
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
}
.product > button{
    background-color: rgba(164, 127, 100, 0.32);
    border: 1px solid black;
    margin-top: 10px;
    border-radius: 6px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111 ;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 1);
}
.product > img {
    max-width: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 25px;
}
.product__price{
   margin-top: 5px;
}
.product__info > p{
    font-size: 15px;
    font-weight: 400;

}
.rating__icon{
    margin-top: 2px;
    background-color:  #fff;
    color: yellow;
}
.product__rating{
    display: flex;
}

.login{
    background-color: #fff;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.login__logo{
    
    margin-top: 20px;
    /* margin-bottom: 20px;  */
    background-color: #111;
    object-fit: contain;
    width: 100px;
    padding: 5px 2px 5px 2px;
    border-radius: 8px;
    margin-left: auto; 
    margin-right: auto; 

}
.login__container{
   width: 300px;
   height: -webkit-fit-content;
   height: -moz-fit-content;
   height: fit-content; 
   border: 1px solid lightgray;
   padding: 20px;
   display: flex;
   flex-direction: column;
   margin: 12px;
}
.login__container> h2{
font-size: 20px;
font-weight: 500;
}
.login__container > form > h5 {
    font-size: 15px;
    font-weight: 500;
}
.login__container input {
width: 95%;
height: 30px;
margin-bottom: 10px;
border-radius: 5px;
/* border: none; */
background-color: #fff;
}
.login__container .login__sinup{
background-color: #f0c14b;
border-radius: 3px;
height: 30px;
margin-top: 10px;
width: 100%;
border: 1px solid;
border-color: #a88734 #9c7e31  #844a29;

}
.login__Registeration{
    height: 30px;
    width: 100%;
    border: px solid;
    margin-top: 10px;
    border-radius: 3px;
    border-color: darkgray;
}
.login__container > p{
    margin-top: 8px;
    font-size: 14px;
    font-weight: 400;
}
.new_to_amazon{
    color: lightgray;
    display: flex;
   margin: 15px;
}
.new_to_amazon > .f__dash{
    position:relative;
    top: -8px ;
    margin: 2px;
}
.new_to_amazon > .s__dash{
    position: relative;
    top: -8px ;
    margin: 2px;
}
.checkout{
    display: flex;
    background-color: #fff;
    padding-bottom: 20px;
}
.checkout__ad{
    margin-top: 0;
    width: 95%;
    height: 80px;
    margin-left: 0;
    margin-right: 5px;
    margin-bottom: 2px;
    box-shadow: 1px 2px 3px  rgb(155, 166, 170);
}
.checkout__title {
    margin-top: 8px;
    margin-left: 15px;
    color: rgb(135, 146, 142);
    padding: 10px;
    border-bottom:2px solid lightgray;
    font-size :30px;
    font-weight: 500;
    cursor:zoom-in;
}
.user__login{
    border: 1px solid black;
    width: 250px;
    height: 60px;
    background-color: lightgray;
    cursor: pointer;
   color: #5e4444;
    box-shadow: 2px 2px 10px 1px rgba(140, 85, 85, 0.6);
    border-radius: 13px;
    margin-left: 8px;
}
.user__login:hover{
    border: 1px solid black;
    width: 250px;
    height: 60px;
    background-color: lightgray;
    cursor: pointer;
   color: blue;
    box-shadow: 2px 2px 10px 1px rgba(140, 85, 85, 0.6);
    border-radius: 13px;
    margin-left: 8px;
    background-color: #fff;
}
.user__login >p{
    display: flex;
    justify-content: center;
    position: relative;
    top: 15px;
    font-size: 20px;
    font-weight: 500;
    
}
.user__login >p:hover{
    display: flex;
    justify-content: center;
    position: relative;
    top: 15px;
    font-size: 15px;
    font-weight: 500;
    border-bottom: 1px solid;
    padding: 5px;
    margin: 10px;
}
.subtotal{
display: flex;
flex-direction: column;
justify-content: space-between;
width: 300px;
height: 100px;
padding: 20px;
background-color: #f3f3f3;
border: 1px solid #dddddd;
border-radius: 3px;
}
.subtotal__gift{
  display: flex;
  align-items: center;
}
.subtotal__gift > input{
  margin-right: 5px;
}
.subtotal > button {
  background-color: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  box-shadow: 6px 2px 3px 2px #dddddd;
}
.checkoutProduct{
    display: flex;
    margin-top: 10px;
    object-fit: contain;
     padding: 10px;
     width: 100%;
     /* border-right: 1px solid #111; */
     margin-left: 3px;
}
.checkoutProduct > img{
    height: 180px;
    width: 180px;
   margin-right: 5px; 
}
.checkoutProduct__content{
    margin-left: 10px;  
}
.checkoutProduct__content > button{
    background-color: rgba(164, 127, 100, 0.32);
    border: 1px solid black;
    margin-top: 10px;
    border-radius: 6px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111 ;
    box-shadow: 1px 1px 1px rgba(0, 0, 0, 1);
}
.checkoutProduct__title{
    font-size: 13px;
}
.checkoutProduct__rating{
    display: flex;
}

.homeHeader{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    
    max-width: 1500px;
    height: 300px;
}
.image{ 
    width: 100%;
     background-position: center;
     background-size: cover;
     background-repeat: no-repeat;
    margin-bottom: -300px;
    -webkit-mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,));
            mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,));
}
.image{
    display: flex;
     width: 100%;
      
    background-size: contain;
    object-fit:cover;
}


.image .left{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    flex: 5% 1;
    height: 250px;

    padding: 15px;
  

}
.image .left:hover{
    cursor: pointer;
    border: 1px solid gray ;
    color: #fff;
    background-color: rgba(0,0,0,0.45) ;
    border: 3px solid #fff;
    
    border-radius: 6px;
     

}
.image .center{
    flex: 90% 1;
}
.image .right{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    flex: 5% 1;
    height: 250px;
   
    padding: 15px;
 
   
}
.image .right:hover{
    /* cursor: pointer; */
    cursor:pointer;
    border: 1px solid gray ;
    background-color:rgba(0,0,0,0.45);
    color: #fff;
    border-radius: 6px;
      border: 3px solid #fff;
}
